      <vs-input type="number" class="w-full mt-5 mb-4" label-placeholder="Avance" @change="Changeadvance"
        v-model="InvoiceData.advance">
      </vs-input>
      <vs-input type="number" class="w-full mt-5 mb-4" label-placeholder="Reste" v-model="InvoiceData.rest">
      </vs-input>
