      <v-select disabled id="items" multiple :closeOnSelect="false" v-model="itemsDataOrderedItems"
        :options="itemsDataOrderedItems" label="item" :dir="$vs.rtl ? 'rtl' : 'ltr'" /><br>
      <div class="mt-4">
