<script>
import moduleDataList from "@/store/data-list/moduleDataList.js"
import vSelect from 'vue-select'
import axios from 'axios'

export default {
  components: {
    'v-select': vSelect,
  },
  data() {
    return {
      activePromptPrintInvoice: false,
      mailTo: "",
      companyDetails: {
        name: 'Société Afrah Bhar',
        addressLine1: 'Ariana',
        zipcode: '2080',
        mailId: 'bhar.events@gmail.com',
        mobile: '70 319 178',
      },
      recipientDetails: {
        fullName: 'Peter Stark',
        addressLine1: '8577 West West Drive ',
        addressLine2: 'Holbrook, NY',
        zipcode: '90001',
        mailId: 'peter@mail.com',
        mobile: '+91 988 888 8888',
      },
      invoiceDetails: {
        invoiceNo: '001/2019',
        invoiceDate: 'Mon Dec 10 2018 07:46:00 GMT+0000 (GMT)',
      },
      invoiceData: {
        tasks: [
          {
            id: 1,
            task: 'Website Redesign',
            hours: 60,
            rate: 15,
            amount: 90000,
          },
          {
            id: 2,
            task: 'Newsletter template design',
            hours: 20,
            rate: 12,
            amount: 24000,
          },
        ],
        subtotal: 114000,
        discountPercentage: 5,
        discountedAmount: 5700,
        total: 108300,
      },
      nbTest: 0,
      activePromptEditInvoice: false,
      InvoiceData: {},
      items_Data: [],
      itemsDataOrdered: [],
      itemsDataOrderedItems: [],
      Invoices: [],
      selected: [],
      // Invoices: [],
      itemsPerPage: 10,
      isMounted: false,

      // Data Sidebar
      addNewDataSidebar: false,
      sidebarData: {},
    }
  },
  computed: {
    currentPage() {
      if (this.isMounted) {
        return this.$refs.table.currentx
      }
      return 0
    },
    queriedItems() {
      return this.$refs.table ? this.$refs.table.queriedResults.length : this.Invoices.length
    }
  },
  methods: {
    activePromptPrint() {
      this.activePromptPrintInvoice = true
    },
    printInvoice() {
      window.print()
    },
    async GetInvoices() {
      await axios
        .get("/invoices/", {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then((res) => {
          this.Invoices = res.data;
        });
    },
    GetInvoice(InvoiceData) {
        this.InvoiceData = InvoiceData
        this.items_Data = this.InvoiceData.items_Data
        this.items_Data.map((item) => {
          this.itemsDataOrderedItems.push(item.orderedItemData)
        })
        this.activePromptEditInvoice = true
    },
    Changeadvance() {
      this.InvoiceData.rest = this.InvoiceData.total_ttc - this.InvoiceData.advance
    },
    async PutInvoice() {
      this.items_Data.map((item) => {
        this.InvoiceData.items.push(item.id)
      })
      if (this.InvoiceData.rest == 0) {
        this.InvoiceData.payment_status = "Payée"
      }else {
        this.InvoiceData.payment_status = "Impayée"
      }
      await axios
        .put("/invoices/" + this.InvoiceData.id + "/", this.InvoiceData, {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then(() => {
          this.$vs.notify({
            title: "Validation avec succès",
            color: "success",
          });
          this.GetInvoices();
          this.activePromptEditInvoice = false;
        })
        .catch(() => {
          this.$vs.notify({
            title: "Erreur de modification",
            text: "veuillez vérifier vos informations",
            iconPack: "feather",
            icon: "icon-alert-circle",
            color: "danger",
          });
          this.activePromptEditInvoice = true;
        });
    },
    DeleteInvoice(tr) {
      this.InvoiceData = tr
      this.$vs.dialog({
        type: "confirm",
        color: "danger",
        title: `Confirmation de la suppression`,
        text: `Supprimer "${tr.number}"`,
        acceptText: "Supprimer",
        accept: this.ConfirmDeleteInvoice,
        cancelText: "Annuler",

      });
    },
    async ConfirmDeleteInvoice() {
      await axios
        .delete("/invoices/" + this.InvoiceData.id + "/", {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then(() => {
          this.$vs.notify({
            title: "Suppression avec succès",
            color: "success",
          });
          this.GetInvoices();
          this.activePromptEditInvoice = false;
        })
        .catch(() => {
          this.$vs.notify({
            title: "Suppression avec Suppression",
            text: "veuillez essayer plus tard",
            iconPack: "feather",
            icon: "icon-alert-circle",
            color: "danger",
          });
          this.activePromptEditRealStock = true;
        });
    },
    getOrderStatusColor(status) {
      if (status == 'En attente') return "warning"
      if (status == 'Confirmée') return "success"
      if (status == 'Annulée') return "danger"
      return "primary"
    },
    getPaymentStatusColor(status) {
      if (status == 'Payée') return "success"
      if (status == 'Impayée') return "danger"
      return "primary"
    },
    getPopularityColor(num) {
      if (num > 90) return "success"
      if (num > 70) return "primary"
      if (num >= 50) return "warning"
      if (num < 50) return "danger"
      return "primary"
    },
    toggleDataSidebar(val = false) {
      this.addNewDataSidebar = val
    }
  },
  created() {
    if (!moduleDataList.isRegistered) {
      this.$store.registerModule('dataList', moduleDataList)
      moduleDataList.isRegistered = true
    }
  },
  mounted() {
    this.isMounted = true;
    this.$emit("setAppClasses", "invoice-page")
    this.GetInvoices()
  }
}
</script>

